'use client';

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/lib/auth-context';
import JobCard from '@/components/JobCard';
import { 
  TrendingUp, Users, Briefcase, DollarSign, 
  Clock, AlertCircle, ArrowRight, Calendar
} from 'lucide-react';

export default function AdminDashboard() {
  const router = useRouter();
  const { getToken } = useAuth();
  const [stats, setStats] = useState({
    totalJobs: 0,
    pendingJobs: 0,
    activeJobs: 0,
    completedJobs: 0,
    totalRevenue: 0,
    totalClients: 0,
    totalStaff: 0,
  });
  const [recentBookings, setRecentBookings] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      const token = getToken();
      const headers = {};
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      // Fetch all bookings
      const bookingsRes = await fetch('/api/bookings', {
        headers,
      });
      
      if (bookingsRes.ok) {
        const bookingsData = await bookingsRes.json();
        const bookings = bookingsData.bookings || [];
        
        setStats({
          totalJobs: bookings.length,
          pendingJobs: bookings.filter(b => b.status === 'pending').length,
          activeJobs: bookings.filter(b => b.status === 'in_progress' || b.status === 'scheduled').length,
          completedJobs: bookings.filter(b => b.status === 'completed').length,
          totalRevenue: bookings.reduce((sum, b) => sum + (b.total_cost || 0), 0),
          totalClients: new Set(bookings.map(b => b.business_id)).size,
          totalStaff: 0, // Would need separate API call
        });
        
        setRecentBookings(bookings.slice(0, 5));
      }
    } catch (error) {
      console.error('Fetch dashboard error:', error);
    } finally {
      setLoading(false);
    }
  };

  const statCards = [
    { label: 'Total Jobs', value: stats.totalJobs, icon: Briefcase, color: 'blue' },
    { label: 'Pending', value: stats.pendingJobs, icon: Clock, color: 'yellow' },
    { label: 'Active', value: stats.activeJobs, icon: TrendingUp, color: 'purple' },
    { label: 'Completed', value: stats.completedJobs, icon: Calendar, color: 'green' },
    { label: 'Total Revenue', value: `₦${stats.totalRevenue.toLocaleString()}`, icon: DollarSign, color: 'green' },
    { label: 'Clients', value: stats.totalClients, icon: Users, color: 'blue' },
  ];

  return (
    <div className="space-y-8">
      <h1 className="text-4xl md:text-5xl font-black uppercase tracking-tighter italic text-black">Admin Dashboard</h1>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {statCards.map((stat, index) => (
          <div
            key={index}
            className="p-6 rounded-2xl bg-white border border-gray-200 hover:bg-gray-50 hover:border-red-300 transition-all shadow-sm"
          >
            <div className="flex items-center justify-between mb-4">
              <stat.icon size={24} className="text-red-600" />
            </div>
            <p className="text-sm font-bold uppercase tracking-wider text-gray-600 mb-1">{stat.label}</p>
            <p className="text-2xl font-black text-black">{stat.value}</p>
          </div>
        ))}
      </div>

      {/* Recent Bookings */}
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-2xl font-black uppercase tracking-tight italic text-black">Recent Bookings</h2>
          <button
            onClick={() => router.push('/admin/jobs')}
            className="text-sm font-bold uppercase tracking-wider text-red-600 hover:text-red-700 transition-colors flex items-center gap-2"
          >
            View All
            <ArrowRight size={16} />
          </button>
        </div>
        {loading ? (
          <div className="text-center py-12 text-gray-600">Loading...</div>
        ) : recentBookings.length > 0 ? (
          <div className="space-y-4">
            {recentBookings.map((booking, index) => (
              <JobCard key={booking.id} job={booking} index={index} onClick={() => router.push(`/admin/jobs/${booking.id}`)} />
            ))}
          </div>
        ) : (
          <div className="text-center py-12 text-gray-600">No bookings yet.</div>
        )}
      </div>
    </div>
  );
}
